<template>
  <div id="mixcm-footer">
    <div class="mixcm-copyright">Copyright &copy;
      {{ year }} Mixcm. All rights reserved.</div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        year: new Date().getFullYear()
      }
    },
  }
</script>

<style>
#mixcm-footer {
    opacity: .9;
    background-image: url(https://i.loli.net/2018/08/25/5b810426c3e1b.jpg);
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
    padding: 8em 0 8em 0;
    text-align: center;
    color: #fff;
    overflow: hidden;
    position: relative;
}

#mixcm-footer:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(45deg, #a480ff, #ff8f83);
    opacity: 0.5;
    z-index: 1;
}

#mixcm-footer * {
    z-index: 2;
    position: relative;
}
</style>



