import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Ranking from '@/components/Ranking'
import About from '@/components/About'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    { path: '/', component: Home, meta: {title: 'Mixcm - 海纳百川,有容乃大'}  },
    { path: '/pages/ranking/:type', component: Ranking, meta: {title: '排行 - Mixcm'} },
    { path: '/pages/about', component: About, meta: {title: '关于 - Mixcm'} },
    { path: '/pages/about/:name', component: About, meta: {title: '关于 - Mixcm'} },
    { path: '*', component: About, meta: {title: '找不到页面'} }

  ]
})



