<template>
  <div id="mixcm-content">
    <div class="mixcm-container">
      <div class="mixcm-banner mixcm-item mixcm-item-col-12">
        <div class="mixcm-banner-item">
          <div class="mixcm-background" style="background-image: url(https://i.loli.net/2018/04/14/5ad1ca5b1064d.jpg);">
          </div>
          <div class="mixcm-content">
            <h1>
              <span style="font-size: 3em;">Mixcm</span>
              <br>海纳百川,有容乃大
            </h1>
          </div>
        </div>
      </div>
    </div>

    <div class="mixcm-container">
      <div class="mixcm-item mixcm-item-col-12 mixcm-project">
        <div v-for="item in items" :key="item.time" class="mixcm-item-col-4 mixcm-card-bk-name">
          <div class="mixcm-background" v-bind:style="{backgroundImage:'url(' + item.pic + ')'}">
          </div>
          <a :href="item.site" target="_blank">
            <div class="mixcm-major">
              <h3>{{ item.title }}</h3>
              <p>{{ item.intro }}</p>
            </div>
          </a>
        </div>
      </div>
    </div>


  </div>
  <!-- #mixcm-content -->
</template>

<script>
  export default {
    data() {
      return {
        items: 'Welcome to Your Vue.js App'
      }
    },
    created: function () {
      this.axios.get('https://mixcm.com/home.json')
        .then((res) => {
          this.items = res.data;
        })
        .catch(function (error) {
          console.log(error)
        });
    }
  }

</script>



