<template>
  <div id="app">
    <Header/>
    <router-view/>
    <Feedback/>
    <Footer/>
  </div>
</template>

<script>
import Header from './components/Header'
import Feedback from './components/Feedback'
import Footer from './components/Footer'
export default {
  name: 'App',
  components: {
    Header,
    Footer,
    Feedback,
  },
}
import router from './router'
router.beforeEach((to, from, next) => {
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})
</script>

<style>

 * {
    text-shadow: 0 0;
}

body {
    margin: 0;
    background-color: #f2f5fa;
}

b, strong {
    border-radius: 2px;
    padding: 0 5px;
    border: 1px solid;
    color: #e06870;
    line-height: 26px;
    margin-right: 5px;
}

a {
  text-decoration: none;
  color: inherit;
}

.mixcm-container {
    max-width: 1080px;
    margin: auto;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    font-family: "SF Pro SC", "SF Pro Display", "SF Pro Icons", "AOS Icons", "PingFang SC", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
}

.mixcm-container-full {
    width: 100%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    font-family: "SF Pro SC", "SF Pro Display", "SF Pro Icons", "AOS Icons", "PingFang SC", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
}

.mixcm-container::after,
.mixcm-container-full::after {
    display: table;
    clear: both;
    content: '';
    height: 0;
}

.mixcm-item {
    border-radius: 1px;
    background-color: #fff;
}

.mixcm-item-col-3 {
    width: calc(25% - 22.5px)
}

.mixcm-item-col-4 {
    width: calc(33.33333333333% - 20px)
}

.mixcm-item-col-6 {
    width: calc(50% - 10px);
}

.mixcm-item-col-12 {
    width: 100%;
}

.mixcm-item-col-3:nth-child(4n+4) {
    margin-right: 0;
}

.mixcm-item-col-4:nth-child(3n+3) {
    margin-right: 0;
}

.mixcm-item-col-6:nth-child(2n+2) {
    margin-right: 0;
}

[class*=mixcm-item-col-] {
    float: left;
    margin: 0 30px 30px 0;
}

a.nexmoe-ui {
    text-decoration: none;
    color: #e06870;
    position: relative;
}

a.nexmoe-ui:after {
    content: ">";
    margin-left: 5px;
    display: inline-block;
    font-family: cursive;
    font-weight: 800;
}

a.nexmoe-ui:before {
    -webkit-transition: all .35s;
    transition: all .35s;
    content: "";
    right: 50%;
    width: 0;
    bottom: -2px;
    position: absolute;
    border-bottom: 1px solid;
}

a.nexmoe-ui:hover:before {
    right: 0;
    width: 100%;
}
</style>



