<template>
  <div id="mixcm-header" class="mixcm-item">
    <nav class="mixcm-nav">
      <div class="mixcm-container">
        <div class="mixcm-tab">
          <el-menu router :default-active="$route.path" class="el-menu-demo" mode="horizontal">
            <el-menu-item index="/">
              首页
            </el-menu-item>
            <!--<el-submenu>
              <template slot="title">排行</template>
              <el-menu-item index="/pages/ranking/last_seen_time">
                最近登录
              </el-menu-item>
              <el-menu-item index="/pages/ranking/join_time">
                最新注册
              </el-menu-item>
              <el-menu-item index="/pages/ranking/experience">
                经验总榜
              </el-menu-item>
            </el-submenu>-->
            <el-menu-item index="/pages/about">
              关于
            </el-menu-item>
          </el-menu>

        </div>
        <div class="mixcm-notice">
          <a class="mdui-btn mdui-btn-icon mdui-ripple">
            <i style="font-size: 24px;" class="mixcmfont icon-notice"></i>
          </a>
        </div>
        <div class="mixcm-avatar">
          <img src="https://avatars2.githubusercontent.com/u/16796652">
        </div>
      </div>
    </nav>

    <div class="mixcm-banner">
      <div class="mixcm-background" style="background-image: url(https://i.loli.net/2018/04/01/5abfb3d0e4575.jpg); opacity: 0.15;">
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    name: 'Header',
  }

</script>

<style>
#mixcm-header {
    width: 100%;
    position: relative;
}

#mixcm-header .mixcm-nav {
    width: 100%;
    color: #454d5d;
    position: fixed;
    background-color: rgba(255, 255, 255, .93);
    -webkit-box-shadow: 0 0 0.5em 0 rgba(161, 177, 204, 0.325);
    box-shadow: 0 0 0.5em 0 rgba(161, 177, 204, 0.325);
    z-index: 10000;
    top: 0;
}

#mixcm-header .el-menu--horizontal>.el-submenu .el-submenu__icon-arrow {
  display: none;
}

.el-menu--horizontal {
  z-index: 10000!important;
}

.el-menu--horizontal ul:before {
  transform: rotate(45deg);
  background-color: #fff;
  box-shadow: -2px -2px 3px -1px rgba(0,0,0,.1);
  content: "";
  position: absolute;
  top: -.25rem;
  left: 2.5em;
  width: .75rem;
  height: .75rem;
}

.el-menu.el-menu--horizontal,
.el-menu--horizontal>.el-menu-item.is-active,
.el-menu--horizontal>.el-submenu.is-active .el-submenu__title {
  border-bottom: none!important;
}


.el-menu--horizontal>.el-menu-item,
.el-menu--horizontal>.el-submenu .el-submenu__title {
  padding: 0;
  height: 68px;
  line-height: 68px;
  min-width: 90px;
  text-align: center;
}

#mixcm-header .mixcm-nav .is-active {
    color: #e59dc4;
    position: relative;
}

#mixcm-header .mixcm-nav .is-active:after {
    content: '';
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background-image: -webkit-gradient(linear, left top, right top, from(#a480ff), to(#ff8f83));
    background-image: linear-gradient(90deg, #a480ff, #ff8f83);
    -webkit-box-shadow: 0 0 5px;
    box-shadow: 0 0 5px;
}

#mixcm-header .mixcm-nav .mixcm-tab {
    width: calc(100% - 96px);
    min-height: 68px;
    overflow: unset;
}

#mixcm-header .mixcm-nav .mixcm-tab ul {
    background: transparent;
}

#mixcm-header .mixcm-nav .mixcm-tab a {
  display: block;
}

#mixcm-header .mixcm-nav .mixcm-logo {
    width: 96px;
    height: 48px;
}

#mixcm-header .mixcm-nav .mixcm-logo a {
    display: block;
    width: 100%;
    height: 100%;
    background: #fff;
    color: #454d5d;
    line-height: 48px;
    font-size: 1.4em;
    text-align: center;
    text-decoration: none;
}

#mixcm-header .mixcm-nav .mixcm-avatar,
#mixcm-header .mixcm-nav .mixcm-notice {
    width: 48px;
    height: 48px;
    line-height: 48px;
    text-align: center;
    border-radius: 100%;
    overflow: hidden;
    margin-top: 10px;
}

#mixcm-header .mixcm-nav .mixcm-tab,
#mixcm-header .mixcm-nav .mixcm-avatar,
#mixcm-header .mixcm-nav .mixcm-notice {
    float: left;
}

#mixcm-header .mixcm-nav .mdui-fab-wrapper {
    padding: 0!important;
    position: relative!important;
    bottom: 0;
    right: 0;
    height: 68px;
    width: auto;
}

#mixcm-header .mixcm-nav .mdui-fab-wrapper .mdui-fab {
    border-radius: 0;
    box-shadow: none;
    min-height: 68px;
}

#mixcm-header .mixcm-nav .mdui-fab-wrapper .mdui-fab:hover {
    background: transparent;
}

#mixcm-header .mixcm-nav .mdui-fab-wrapper .mdui-fab-dial {
    bottom: unset;
    box-shadow: 0 0.075rem 0.75rem 0 rgba(161, 177, 204, 0.325);
    background: #fff;
    border-radius:  1px;
    margin-top:  10px;
    padding: 0 10px;
    left: -10px;
    right: unset;
    display: none;
    height: auto!important;
}

#mixcm-header .mixcm-nav .mdui-fab-dial-show {
    animation-name: menu-nav-appear;
    animation-duration: 0.35s;
    display: block!important;
}

@keyframes menu-nav-appear {
    0% {
        transform: translateY(-15px);
        opacity: 0;
    }
}

#mixcm-header .mixcm-nav .mdui-fab-wrapper .mdui-fab-dial::before {
    content: " ";
    height: 10px;
    width: 100%;
    position: absolute;
    top: -10px;
    left: 0;
    z-index: 3;
}

#mixcm-header .mixcm-nav .mdui-fab-wrapper .mdui-fab-dial:after {
    -moz-transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    background-color: #ffffff;
    box-shadow: -2px -2px 3px -1px rgba(0, 0, 0, 0.1);
    content: '';
    position: absolute;
    top: -0.25rem;
    right: 3.25em;
    width: 0.75rem;
    height: 0.75rem;
}

#mixcm-header .mixcm-nav .mdui-fab-wrapper .mdui-fab-mini {
    margin: 0;
    width: auto;
    text-align: center;
    position: relative;
    min-height: 48px;
    padding: 14px!important;
    -webkit-transform: scale(1);
    transform: scale(1);
    border-bottom: 1px dashed #eee;
}

#mixcm-header .mixcm-nav .mdui-fab-wrapper .mdui-fab-mini:hover {
    color: #e59dc4;
}


#mixcm-header .mixcm-nav .mdui-fab-wrapper .mdui-fab-mini:last-child {
    border-bottom: none;
}

#mixcm-header .mixcm-nav .mixcm-avatar img {
    width: 100%;
}

#mixcm-header .mixcm-banner {
    height: 300px;
    background-color: #fff;
    overflow: hidden;
    margin-top: 68px;
}

#mixcm-header .mixcm-background {
    height: calc(100% + 60px);
    background-size: cover;
    background-position: center;
    -webkit-filter: blur(15px);
    filter: blur(15px);
    margin: -30px;
}
</style>



