<template>
  <div id="mixcm-content">
    <div class="mixcm-container">
      <div class="mixcm-item mixcm-item-col-12 mixcm-article">
        <div class="mixcm-header">
          <h1>Mixcm</h1>
          <p>如果要中文名的话，那就叫“自由之翼”好了。</p>
        </div>
        <div class="mixcm-content">
          <div class="mixcm-about-partner">
            <router-link v-for="item in items" :key="item.id" :to="'/pages/about/'+item.name">
              <img :src='item.avatar'>
            </router-link>
            <p>{{ bio }}</p>
          </div>
          <div style="display: none;" v-for="item in items" :key="item.id" :id="item.name">
            <p>{{ item.bio }}</p>
          </div>
          <h2 id="意志相关">意志相关</h2>
          <p>海贼王</p>
          <p><img src="https://i.loli.net/2018/08/26/5b826974cb9da.jpg"></p>
          <p>进击的巨人</p>
          <p><img src="https://i.loli.net/2018/08/26/5b82694e48ebf.jpg"></p>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped>

#mixcm-content .mixcm-about-partner {
    overflow: unset;
    max-height: unset;
    display: block;
    text-align: center;
    white-space: unset;
}

#mixcm-content .mixcm-about-partner:before {
    content: ' ';
    -webkit-box-flex: 1;
    -webkit-flex-grow: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
}

#mixcm-content .mixcm-about-partner:after {
    content: ' ';
    -webkit-box-flex: 1;
    -webkit-flex-grow: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
}

#mixcm-content .mixcm-about-partner > a {
    padding: 0;
    max-width: 62px;
    min-width: 62px;
    border-radius: 100%;
    margin: 0 5px;
    transition: all .2s;
    display: inline-block;
    font-size: 0;
    overflow: hidden;
}

#mixcm-content .mixcm-about-partner .router-link-exact-active {
    color: #3f51b5;
    opacity: 1;
    box-shadow: 0 2px 10px rgba(161, 177, 204, 0.6);
}

#mixcm-content .mixcm-about-partner > a > img {
    width: 100%;
    margin: 0 !important;
    border: none !important;
}
</style>

<script>
  export default {
    data() {
      return {
        items: '',
        bio: '',
      }
    },
    watch: {
      '$route'(to, from) {
        var i = 0;
        while (this.items[i]) {
          if (this.items[i].name == this.$route.params.name) {
            this.bio = this.items[i].bio;
            break;
          }
          i++;
        }
      }
    },
    created: function () {
      this.axios.get('https://mixcm.com/about.json')
        .then((res) => {
          this.items = res.data;
        })
        .catch(function (error) {
          console.log(error)
        });
    }
  }

</script>



